/* fonts
@include font-face("MiniSSF Bold", "../fonts/MINI_Sans_Serif_Bold/MINI_Sans_Serif_Bold"); 
*/
@import url(https://fonts.googleapis.com/css?family=Seaweed+Script);
[class*="border"] {
  border: 1px solid #000 !important; }

.border-5 {
  border: 5px solid #000 !important; }

.border-top {
  border-width: 0 !important;
  border-top: 1px solid #000 !important; }

.border-bottom {
  border-width: 0 !important;
  border-bottom: 1px solid #000 !important; }

.border-top-5 {
  border-width: 0 !important;
  border-top-width: 5px !important; }

.border-bottom-5 {
  border-width: 0 !important;
  border-bottom-width: 5px !important; }

.border-bottom-10 {
  border-width: 0 !important;
  border-bottom-width: 10px !important; }

.h-rotate-180 {
  transform: rotate(180deg); }

.no-padding {
  padding: 0;
  margin: 0; }

html {
  font-size: 62.5%; }

body {
  color: #000;
  margin: 0;
  padding: 0; }

.main-content {
  max-width: 1280px;
  margin: auto;
  overflow: hidden; }

ul, ol, li {
  list-style: none;
  margin: 0;
  padding: 0; }

.no-padding {
  margin: 0;
  padding: 0; }

.h1 {
  line-height: 2.6rem;
  font-size: 2.6rem;
  text-align: left;
  text-transform: uppercase;
  margin-top: 2rem;
  margin-bottom: 2rem; }

@media screen and (max-width: 1199px) {
  .display-desktop {
    display: none !important; } }

@media screen and (min-width: 1200px) {
  .display-mobile {
    display: none !important; } }

.nav-menu {
  display: block; }

.gs-header {
  display: flex;
  justify-content: space-between; }
  .gs-header_logo {
    display: inline-block; }
    .gs-header_logo .gs-logo {
      width: 10rem;
      height: 6rem; }
    .gs-header_logo .gs-slogan {
      font-size: 1.6rem; }
  .gs-header_menu .gs-menu_item {
    font-size: 1.8rem;
    padding: 0.4rem; }

.footer {
  display: block; }

.gs-article {
  font-family: "Source Sans Pro", Helvetica, Verdana, sans-serif; }
  .gs-article_title {
    color: rgba(0, 133, 172, 0.3);
    font-size: 2.4rem; }

.gs-menu_item {
  font-size: 1.4rem;
  padding: 0.4rem; }

fieldset {
  border: 0.2rem solid #c1c1c1;
  padding: 3rem;
  margin: 2rem auto 0 auto;
  width: 80rem;
  max-width: 100rem; }

.gs-form {
  display: grid;
  grid-template-columns: 1fr minmax(auto, 30em) 1fr;
  min-height: 100vh;
  place-items: center;
  justify-items: stretch;
  grid-column: 2; }
  .gs-form_group .label {
    font-size: 1.4rem;
    text-align: right;
    display: block;
    padding: 0.5em 1.5em 0.5em 0; }
  .gs-form_group .form-control {
    align-items: left;
    border: 1px solid #9c9c9c;
    width: 100%;
    padding: 0.4rem;
    margin-bottom: 0.5rem; }
  .gs-form .submit:hover {
    background: green; }

.gs-title {
  text-align: center; }

.gs-span {
  font-family: "Seaweed Script";
  color: royalblue;
  text-align: center;
  font-size: 4.5rem;
  position: relative;
  padding: 0 0.3em; }
